* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  box-sizing: border-box;
}

a {
  color: none;
}

body {
    background-color: #2a2e31;
    font-family: 'Lato', sans-serif, arial;
    color: #e6e1e1;
}


.nav {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #2f3640;
  opacity: 0.7;
  width: 100%;
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  
}


.nav ul li{
  display: inline-block;
  padding: 10px;
}
.nav ul a {
  color: grey;

}
.nav ul a:hover {
  color: royalblue;
}

.nav ul li a.active {
  color: white;
}

.container {
  display: flex;
  flex-direction: row;
    height: 100vh;

}
.pLewy {
  flex-basis: 30vw;
  height: 100vh;
     }

.pPrawy {
height: 100vh;
flex-basis: 100vw;
}

.icons {
  display: none;
  flex-direction: column;
  align-items: center;
  width: 90%;
  margin: auto;
}
.smallIcons {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  
}
.smallIcons span {
 margin: 10px;
text-transform: none;
}

  @media (min-width: 1025px) {
    .nav {
      width: 30%;
      height: 100%;
    font-weight: normal;
    display: flex;
    flex-direction: column;

      
    }
    .icons {
      display: flex;
    }
  }




.main {
    text-align: center;
    color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
    
  }
  
    .title {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      font-size: 35px;
      font-weight: bold;
      text-transform: uppercase;
    }
  
    .title span {
      display: block;
      font-size: 16px;
      font-weight: normal;
    }

    .bgc {
      position: relative;
      overflow: hidden;
      height: 100%;
      left: 0;
    }
    
    .bgc img {
      position: absolute;
      top: 50%;
      left: -100px;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      opacity: 0.4;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    }

    .pPrawy .icons {
      display: flex;
      position: absolute;
      top: 60%;
    }
    @media (min-width: 500px) {
      .bgc img {
        left: 20%;
      }
    }
    @media (min-width: 800px) {
      .bgc img {
        left: 50%;
      }
    }

    @media (min-width: 1025px) {
      .title {
        left: 50%;
      }
      .pPrawy .icons {
        display: none;
        
      }
    }

.containerProjekty {
  position: absolute;
  top: 39px;
  left: 0;
  width: 100%;
  height: calc(100% - 39px);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;

}


.containerProjekty p {
  color: #e6e1e1;
}

.mainProjekty {
  text-align: center;
  color: #e6e1e1;
  display: flex;
  flex-direction: row;
  justify-content: center;
    
}
.containerProjekty .links{
  text-align: center;
}
.containerProjekty .links a{
  padding: 10px;
  margin: 5px;
  background: #e74c3c;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  cursor: pointer;
  transition: 0.5s;
  white-space: nowrap;
  display: inline-block;
  letter-spacing: 1px;

}
.containerProjekty .links a:hover {
  background: #b52d1f;
}


.containerProjekty button{
  padding: 10px;
  background: royalblue;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  cursor: pointer;
    letter-spacing: 1px;
  white-space: nowrap;


}

.containerProjekty .techs {
  color: #e6e1e1;
  text-align: center;
  display: flex;
}
.containerProjekty .techs .tech {
  margin: 5px;
}

  .monitor {
    position: relative;
    width: 90%;
  }

  .monitor img {
    width: 100%;
    max-width: 350px;
  }

  .monitor .screenShot {
    position: absolute;
    top: 6%;
    left: 5%;
    width: 90%;
    height: 56%;
    border-radius: 5px;
  }


  .monitor .fadeOutfadeIn {
    -webkit-animation-name: fadeOutfadeIn;
            animation-name: fadeOutfadeIn;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-duration: 20s;
            animation-duration: 20s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    animation-direction: reverse;
}


@-webkit-keyframes fadeOutfadeIn {
0% {
        opacity: 1;
    }
    10% {
      opacity: 0;
  }
  50% {
    opacity: 0;
}
60% {
  opacity: 1;
}
    
100% {
    opacity: 1;
}
}


@keyframes fadeOutfadeIn {
0% {
        opacity: 1;
    }
    10% {
      opacity: 0;
  }
  50% {
    opacity: 0;
}
60% {
  opacity: 1;
}
    
100% {
    opacity: 1;
}
}
@media (min-width: 600px) {
  .containerProjekty {
    top: 0px;
    left: 0;
    height: 100%;
    width: 70%;
  }
  
  .containerProjekty .nextProject button{
    position: absolute;
    top: 30%;
    left: 90%;
  
  }
  .containerProjekty .links{
    position: absolute;
    top: 70%;
    left: 90%;
  }
}
@media (min-width: 1025px) {
  .containerProjekty {
    top: 0px;
    left: 20%;
    height: 100%;
    width: 70%;
  }
  .containerProjekty p {
    font-size: 32px;
    font-weight: bold;
    letter-spacing: 1px;
  }
  .containerProjekty .nextProject button{
    position: absolute;
    top: 30%;
    left: 80%;
  
  }
  .containerProjekty .links{
    position: absolute;
    top: 70%;
    left: 80%;
  }
}


.loadingImg {
  position: absolute;
    top: 6%;
    left: 5%;
    width: 90%;
    height: 56%;
    border-radius: 5px;
    background-color: #222;
    display: flex;
flex-direction: column;
justify-content: center;
}
